.image-container {
    background-color: #ccc;
    overflow: hidden;
    position: relative;
    max-width: 800px;
    margin: 20px auto;
  }
  .image-container .image {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    opacity: 0;
  }
  .image-container .image.thumb {
    opacity: 1;
    filter: blur(10px);
    transition: opacity 1s ease-in-out;
    position: absolute;
  }
.image-container .image.thumb.isLoaded {
    opacity: 0;
}

.image-container .image.isLoaded {
    transition: opacity 1s ease-in-out;
    opacity: 1;
}
